<template>
  <div class="personalData">
    <van-nav-bar title="个人资料" left-text="返回" left-arrow @click-left="back" />

    <div class="personal-box">
      <van-cell title="头像">
        <div class="fr personal-img">
          <img :src="myData.userImg" alt class="auto-img" />
          <van-uploader :after-read="uploadUserImg" />
        </div>
      </van-cell>
      <van-cell title="用户ID">
        <div class="fr">{{myData.userId}}</div>
      </van-cell>
      <van-cell title="用户名">
        <div class="fr">
          <input
            type="text"
            class="nickname"
            v-model="myData.nickName"
            @change="modify('nickName','/updateNickName')"
          />
        </div>
      </van-cell>
      <van-cell title="手机号">
        <div class="fr">{{myData.phone}}</div>
      </van-cell>
      <van-cell title="会员等级">
        <div class="fr" v-if="myData.vip==0">普通会员</div>
        <div class="fr" v-if="myData.vip==1">钻石会员</div>
      </van-cell>
      <div class="desc-box">
        <div class="desc-title">简介</div>
        <div>
          <textarea
            class="text-area"
            v-model="myData.desc"
            placeholder="这家伙很懒，什么都没有留下"
            @change="modify('desc','/updateDesc')"
          ></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/personalData.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      myData: [],
      maxSize: 500*1024,
    };
  },
  created() {
    this.getMyData();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //获取个人信息
    getMyData() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "B001") {
            this.myData = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //上传头像
    uploadUserImg(file) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      //控制图片类型
      let type = file.file.type.split("/")[1];
      let imgType = ["jpg", "png", "gif", "jpeg"];
      if (imgType.indexOf(type) == -1) {
        this.$toast("仅支持" + imgType.join(","));
        return;
      }
      //控制图片大小
      if (this.maxSize < file.file.size) {
        this.$toast("上传图片大小不能超过" + this.maxSize + "KB");
        return;
      }

      let base64Img = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      let data = {
        appkey: this.appkey,
        tokenString: tokenString,
        imgType: type,
        serverBase64Img: base64Img,
      };
      data = utils.queryString(data);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "H001") {
            this.myData.userImg = result.data.userImg;
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //修改昵称，简介等
    modify(key, url) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }

      let data = {
        appkey: this.appkey,
        tokenString: tokenString,
        [key]: this.myData[key],
      };
      data = utils.queryString(data);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
        

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>